import { createApp } from 'vue';
import App from './assets/pages/IndexRda.vue';
import router from './js/routes.js';
// tradu√ß√£o
import { createI18n } from 'vue-i18n';
// √≠cones
import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

import AOS from 'aos';
import 'aos/dist/aos.css';

import en from './locales/en.json';
import pt from './locales/pt.json';
import es from './locales/es.json';

// Importa Bootstrap CSS e JS
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import './styles/theme.scss';

// importar √≠cones
import {
  faFacebook,
  faInstagram,
  faWhatsapp,
} from '@fortawesome/free-brands-svg-icons';

import { faBars } from '@fortawesome/free-solid-svg-icons';

// Adiciona os √≠cones √† biblioteca
library.add(faFacebook, faInstagram, faWhatsapp, faBars);

// criar inst√¢ncia do Vue
const app = createApp(App);

// Registra o componente global
app.component('font-awesome-icon', FontAwesomeIcon);

app.use(router);

AOS.init({
  duration: 1000, // dura√ß√£o em ms
});

const i18n = createI18n({
  locale: 'pt', // idioma inicial
  fallbackLocale: 'pt', // se n√£o encontrar a chave no idioma atual
  messages: {
    en,
    pt,
    es,
  },
});

app.use(i18n).mount('#app');
